import { Component, OnInit, ViewChild } from '@angular/core';
import { JwtService } from '../../../../shared-ui/jwt.service';
import { currentUser } from '../../../../layouts/home-layout/user.model';
import * as moment from 'moment';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../../shared-ui/global.service';
import { Company } from '../../../../shared-ui/models/global.model';
import { ToastrService } from 'ngx-toastr';
import { ModalDirective } from 'ngx-bootstrap';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  @ViewChild("addGroupModal", { static: false })
  public addGroupModal: ModalDirective;

  currentUser: currentUser = new currentUser;
  selectedGroups: any = [];
  groupForRemove: any = [];
  addedGroup: String = "";
  loadingData: boolean = false;

  constructor(
    private jwtService: JwtService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
    private toastr: ToastrService,
    private router: Router,
  ) {
    this.globalService.setLoadingLabel('Loading... Please Wait.');
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    this.route.params.subscribe((res) => {
      if(res.id){
        this.getCompany(res.id);
      }
    });
  }

  company: Company = new Company;

  ngOnInit() {
  }

  getCompany(id: any){
    this.spinner.show();
    this.loadingData = true;
    let postData = {
      id: id,
      user: this.currentUser.id
    }
    this.globalService.getCompanyById(postData).subscribe(data => {
      if(Object.keys(data).length){
        this.company = data;
        if(this.company.groups && this.company.groups.length){
          this.company.groups.forEach(element => {
            this.selectedGroups.push({ name: element });
          });
        }
      }
      this.spinner.hide();
      this.loadingData = false;
    }, error => {
      this.spinner.hide();
      this.loadingData = false;
      this.toastr.error(environment.reponseCode[error.status], 'Error' );
    });
  }

  saveCompany(){
    this.company.groups = [];
    if(this.selectedGroups.length){
      this.selectedGroups.forEach(element => {
        if(!element.removed){
          this.company.groups.push(element.name);
        }
      });
    }
    let postData = {
      name: this.company.companyName,
      isActive: this.company.isActive ? 1 : 0,
      creator: this.currentUser.id
    }
    if(this.company.groups.length){
      postData['groups'] = this.company.groups;
    }
    this.globalService.setLoadingLabel('Inserting... Please Wait.');
    this.spinner.show();
    this.globalService.saveCompany(postData).subscribe(data => {
      this.spinner.hide();
      if(data && data.companyId){
        this.toastr.success("Data saved successfully.", "Success");
        this.router.navigate(['/company/list']);
      }else{
        this.toastr.error("There are some error while inserting the data!", "Error");
      }
    }, error => {
      this.spinner.hide();
      this.toastr.error(environment.reponseCode[error.status], 'Error' );
    });
  }
  
  updateCompany(){
    this.company.groups = [];
    if(this.selectedGroups.length){
      this.selectedGroups.forEach(element => {
        if(!element.removed){
          this.company.groups.push(element.name);
        }
      });
    }
    let postData = {
      name: this.company.companyName,
      isActive: this.company.isActive ? 1 : 0,
      modifier: this.currentUser.id,
      groups: this.company.groups
    }
    console.log("postData ========= ", postData);
    this.globalService.setLoadingLabel('Updating... Please Wait.');
    this.spinner.show();
    this.globalService.updateCompany(postData, this.company['companyId']).subscribe(data => {
      this.spinner.hide();
      if(data && data.modifiedBy){
        this.toastr.success( "Data updated successfully.", "Success");
        this.router.navigate(['/company/list']);
      }else{
        this.toastr.error("There are some error while updating the data!", "Error");
      }
    }, error => {
      this.spinner.hide();
      this.toastr.error(environment.reponseCode[error.status], 'Error' );
    });
  }
  multiSelect: any;

  multiSelectGroup(multiSelect: any){
    this.groupForRemove = [];
    this.selectedGroups.forEach(element => {
      if(multiSelect){
        element.added = false;
        element.removed = false;
      }else{
        element.added = true;
        this.groupForRemove.push(element);
      }
    });
  }

  selectGroup(group){
    let found = this.groupForRemove.filter(e => e.name == group.name);
    if(found.length){
      this.groupForRemove.splice(this.groupForRemove.indexOf(found[0]), 1);
    }else{
      this.groupForRemove.push(group);
    }
  }

  showAddGroupModal(){
    this.addedGroup = "";
    this.addGroupModal.show();
  }
  
  addGroup(){
    let found = this.selectedGroups.filter(e => e.name == this.addedGroup);
    if(found.length){
      this.toastr.info("This group is already exists!", 'Info');
      return;
    }else{
      this.selectedGroups.push({ name: this.addedGroup });
      this.addedGroup = "";
    }
  }

  deleteGroup() {
    this.selectedGroups.forEach(element => {
      let found = this.groupForRemove.filter(e => e.name == element.name);
      if(found.length){
        element.removed = true;
      }
    });
  }
  
}
